import { PizzaBlock, Categories, SortPopup, Carousels, Loader } from '../components'

const categoryNames = ['–í—Å–µ', '–ú—è—Å–Ω—ã–µ', '–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è', '–ì—Ä–∏–ª—å', '–û—Å—Ç—Ä—ã–µ', '–ó–∞–∫—Ä—ã—Ç—ã–µ']
const sortNames = ['–ø–æ–ø—É–ª—è—Ä–Ω—ã–µ', '—Ü–µ–Ω–∞', '–∞–ª—Ñ–∞–≤–∏—Ç']

const Home = ({ pizzas, loader }) => {
  return (
    <div className="custom-container">
      <Carousels />
      <div className="wrapper">
        <div className="content__top">
          <Categories categoryNames={categoryNames} />
          <SortPopup sortNames={sortNames} />
        </div >
        <h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
        {loader || !pizzas
          ? <Loader />
          : <div className="content__items">
            {pizzas &&
              pizzas.map(pizza => (
                <PizzaBlock
                  key={pizza.id}
                  {...pizza}
                />
              ))
            }
          </div>
        }
      </div>
    </div>
  )
}

export default Home;